import React from 'react';
import {Link} from 'react-router-dom';

function Prva() {
  return (
    <div className="App">

        <table border = '0'>
            <tr>
                <td>
                <Link to = '/druga'>Pritisni za odlazak na drugu stranicu</Link>
                </td>

                <td>
                <Link to = '/treca'>Pritisni za odlazak na trecu stranicu</Link>
                </td>
            </tr>

        </table>

    </div>
  );
}

export default Prva;
